@import url(https://fonts.googleapis.com/css?family=Bad+Script|Open+Sans:400,700);
html {
  background-color: #ccf5f0;
  background: -webkit-gradient(linear, left top, right top, from(#ccf5f0), to(#bca5ef));
  background: linear-gradient(90deg, #ccf5f0, #bca5ef);
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  line-height: 1.4;
  margin: 0;
  position: relative;
}
html:before {
  -webkit-mask-image: -webkit-gradient(linear, left top, left bottom, from(transparent), to(black));
  -webkit-mask-image: linear-gradient(to bottom, transparent, black);
          mask-image: -webkit-gradient(linear, left top, left bottom, from(transparent), to(black));
          mask-image: linear-gradient(to bottom, transparent, black);
  background: -webkit-gradient(linear, left top, right top, from(#ea67b3), to(#a9f3ea));
  background: linear-gradient(90deg, #ea67b3, #a9f3ea);
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
body {
  margin: 0 auto;
  max-width: 1024px;
  min-height: 100vh;
}
h1, h2, h3, h4, h5, h6 {
  margin: 0;
}
h1, h2, h3 {
  font-weight: 400;
}
h4, h5, h6 {
  font-weight: 700;
}
h1 {
  font-size: 2rem;
}
h2 {
  font-size: 1.75rem;
}
h3 {
  font-size: 1.5rem;
}
h4 {
  font-size: 1.25rem;
}
p {
  margin: 1rem 0;
}
.spacing {
  margin: 1rem 0;
}
.spacing-top {
  margin-top: 1rem;
}
.spacing-bottom {
  margin-bottom: 1rem;
}
section {
    padding: 2.5rem;
}
.flex {
    display: flex;
    align-items: center;
}
.script {
    font-family: 'Bad Script', sans-serif;
}

.Social-Nav {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex: 100% 1;
    position: absolute;
    right: 2em;
    top: 2em;
}
.Social-Item, .Button {
    border: 2px solid black;
    border-radius: 50%;
    margin: 0 3px;
    padding: 5px;
    -webkit-transition: .3s;
    transition: .3s
}
.Social-Item:hover, .Button:hover {
    background: #2a8ea5;
    border-color: #2a8ea5;
    color: #ccf5f0 !important;
    cursor: pointer;
}
.Social-Item:hover .Social-Icon {
    -webkit-filter: invert(1);
            filter: invert(1);
}
.Button {
    border-radius: 5px;
    font-size: 1.2em;
    line-height: 1.2em;
    margin-left: .5rem;
    text-decoration: none;
}
.Social-Icon {
    background-position: .25px center !important;
    background-repeat: no-repeat !important;
    background-size: contain !important;
    display: block;
    height: 1.2em;
    -webkit-transition: .3s;
    transition: .3s;
    width: 1.2em;
}

.desc:not(.show) {
    display: none;
}
